import React from "react";
import "./App.css";
import Card from "./card.js";
import { useState } from "react";

function App() {
  // const [productDetails, setProductDetails] = useState([
  let productDetails = [
    {
      name: "Item 1",
      cost: "20",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
    },
    {
      name: "Item 2",
      cost: "30",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
    },
    {
      name: "Item 3",
      cost: "40",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
    },

    {
      name: "Item 4",
      cost: "50",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
    },

    {
      name: "Item 5",
      cost: "60",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
    },
    {
      name: "Item 6",
      cost: "70",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
    },
  ];

  const [cartItems, setCartitems] = useState([]);

  const [total, setTotal] = useState(0);

  function handleclick(addproduct, index) {
    {
      setCartitems([...cartItems, addproduct]);
      let newTotal = total + +addproduct.cost;
      setTotal(Math.ceil(newTotal));
    }
  }
  function handleRemove(itemIndex, addproduct) {
    cartItems.splice(itemIndex, 1);
    setCartitems([...cartItems]);

    // console.log(addproduct.cost)
    let newTotal = total - +addproduct.cost;
    setTotal(Math.ceil(newTotal));
  }
  return (
    <>
      <div className="container mt-5">
        <div className="row">
          <div className="col-3">
            <div className="row m-5">
              <strong>Items in Cart:</strong>
              {cartItems.map((addproduct, index) => {
                return (
                  <li class="list-group-item d-flex justify-content-between align-items-center">
                    {addproduct.name} - ${addproduct.cost}
                    <button
                      class="btn btn-sm btn-warning"
                      onClick={() => handleRemove(index, addproduct)}
                    >
                      X
                    </button>
                  </li>
                );
              })}
            </div>
            <div className="Total">
              <strong>Total: </strong>
              {total}
            </div>
          </div>
          <div className="col-9">
            <div className="row">
              {productDetails.map((Details, index) => {
                return (
                  <Card
                    key={index}
                    detail={Details}
                    clicked={handleclick}
                  ></Card>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;

